import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Title from './title'
import Aux from './aux'

class WorkExperience extends Component {
  static propTypes = {
    workexperience: PropTypes.array
  }

  displayExperience = () => {
    return this.props.workexperience.map((item, index) => {
      const { name, description, image } = item.company
      const projects = item.projects.map((project, index) => {
        return (
          <Project key={index} project={project} />
        )
      })
      return (
        <Aux key={index}>
          <CompanyHeader description={description} image={image}>{name}</CompanyHeader>
          {projects}
        </Aux>
      )
    })
  }

  render () {
    return (
      <div className="work-experience">
        <Title>Work Experience</Title>
        {this.displayExperience()}
      </div>
    )
  }
}

const CompanyHeader = ({ children, description, image }) => {
  return (
    <div className="company-header">
      <div className="company-header__description">
        <h2>{children}</h2>
        <p><em>{description}</em></p>
      </div>
      <div className="company-header__image">
        <img src={image} alt="company-logo" />
      </div>
    </div>
  )
}

CompanyHeader.propTypes = {
  children: PropTypes.node,
  description: PropTypes.string,
  image: PropTypes.string
}

const Project = ({ project }) => {
  const { title, task, skills, image } = project
  return (
    <div className="project">
      <div className="project__information">
        <h3 className="project__title">{title}</h3>
        <p className="project__description">{task}</p>
        <p className="project__skills"><strong>Skills: </strong>{skills}</p>
      </div>
      <div className="project__logo">
        <img src={image} alt="project-logo" />
      </div>
    </div>
  )
}

Project.propTypes = {
  project: PropTypes.object
}

export { CompanyHeader, Project }

export default WorkExperience
