import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import nlflag from '../../assets/img/nlflag-bn.png'
import enflag from '../../assets/img/enflag-bn.png'

const Header = ({ children, toNetherlands, toEnglish, language }) => (
  <header className="header">
    <div className="header__flags">
      <img
        src={nlflag}
        alt="nl-flag"
        onClick={toNetherlands}
        className={classnames(language === 'dutch' ? 'active' : null)} />
      <img
        src={enflag}
        alt="en-flag"
        onClick={toEnglish}
        className={classnames(language === 'english' ? 'active' : null)} />
    </div>
    <span className="header__title">
      <h1>{children}</h1>
    </span>
  </header>
)

Header.propTypes = {
  children: PropTypes.string,
  toEnglish: PropTypes.func,
  toNetherlands: PropTypes.func,
  language: PropTypes.string
}

export default Header
