import React, { Component } from 'react'
import Header from './header'
import Personalia from './personalia'
import Expertise from './expertise'
import Description from './description'
import Links from './links'
import Education from './education'
import WorkExperience from './workexperience'
import data from '../data/data.json'
import Languages from './languages'
import Footer from './footer'
import ModalDiploma from './modal'

const [{ Dutch }, { English }] = data

class App extends Component {
  state = {
    language: 'dutch',
    personalia: Dutch.personalia,
    personalprofile: Dutch.personalprofile,
    workexperience: Dutch.workexperience,
    showmodal: false
  }

  toNetherlands = () => {
    this.setState({
      language: 'dutch',
      personalia: Dutch.personalia,
      personalprofile: Dutch.personalprofile,
      workexperience: Dutch.workexperience
    })
  }

  toEnglish = () => {
    this.setState({
      language: 'english',
      personalia: English.personalia,
      personalprofile: English.personalprofile,
      workexperience: English.workexperience
    })
  }

  render () {
    const { language, personalia, personalprofile, workexperience } = this.state
    return (
      <div className="container">
        <Header
          toEnglish={() => this.toEnglish()}
          toNetherlands={() => this.toNetherlands()}
          language={language}>Front-end Developer</Header>
        <Personalia personaldata={personalia} />
        <Links />
        <Expertise basiskennis={personalprofile} />
        <Description personalprofile={personalprofile} />
        <Education open={() => this.setState({ showmodal: true })} />
        <WorkExperience workexperience={workexperience} />
        <Languages />
        <Footer />
        <ModalDiploma
          show={this.state.showmodal}
          close={() => this.setState({ showmodal: false })} />
      </div>
    )
  }
}

export default App
