import React from 'react'
import Modal from 'react-modal'
import PropTypes from 'prop-types'
import dipjs from '../../assets/img/diploma-javascript.jpg'
import dipreact from '../../assets/img/diploma-react.jpg'

const ModalDiploma = ({ show, close }) => {
  const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      width: '85%',
      height: '80%'
    },
    overlay: {
      backgroundColor: '#0c0c0cc7'
    }
  }

  return (
    <Modal
      isOpen={show}
      onRequestClose={close}
      style={customStyles}
      contentLabel="modaldiploma">
      <div className="modal">
        <img src={dipjs} alt="javascript-diploma" className="modal__image" />
        <img src={dipreact} alt="react-diploma" className="modal__image" />
        <button
          onClick={close}
          className="modal__button">&#10005;</button>
      </div>
    </Modal>
  )
}

ModalDiploma.propTypes = {
  show: PropTypes.bool,
  close: PropTypes.func
}

export default ModalDiploma
